import axios from 'axios';
import Cookies from 'js-cookie';

axios.defaults.xsrfCookieName = 'csrftoken';
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN';

export default ({
  namespaced: true,
  state: {
    userInfo: {
      id: Cookies.get('userInfo.id') || 0,
      username: Cookies.get('userInfo.username') || '',
      firstName: Cookies.get('userInfo.firstName') || '',
      lastName: Cookies.get('userInfo.lastName') || '',
      email: Cookies.get('userInfo.email') || '',
      phone: Cookies.get('userInfo.phone') || '',
    },
    userKey: Cookies.get('userKey') || '',
  },
  mutations: {
    updateInfo(state, newInfo) {
      state.userInfo = newInfo;
      Cookies.set('userInfo', newInfo, { expires: 30 });
    },
    updateUserKey(state, newKey) {
      state.userKey = newKey;
      Cookies.set('userKey', newKey, { expires: 30 });
    },
  },
  actions: {
    logout({ commit, state }) {
      axios({
        url: 'http://127.0.0.1:8000/api/auth/token/logout/',
        method: 'post',
        headers: {
          Authorization: `Token ${state.userKey}`,
        },
      }).then().catch(console.error.data);
      commit('updateUserKey', '');
      commit('updateInfo', {});
    },
    getUser({ commit, state }) {
      axios({
        url: 'http://127.0.0.1:8000/api/auth/users/me/',
        method: 'get',
        headers: {
          Authorization: `Token ${state.userKey}`,
        },
      }).then((result) => {
        const resultInfo = {
          id: result.data.id,
          username: result.data.username,
          firstName: result.data.first_name,
          lastName: result.data.last_name,
          email: result.data.email,
          phone: result.data.phone,
        };
        console.log(resultInfo);
        console.log(result);
        commit('updateInfo', resultInfo);
      }).catch(console.error.data);
    },
  },
  getters: {
    userInfo() {
      // return state.userInfo;
      // console.log(state);
      return Cookies.get('userInfo');
    },
    userKey(state) {
      return state.userKey;
    },
    isAuthenticated(state) {
      return !!state.userKey;
    },
    loginName() {
      return Cookies.get('userInfo.firstName') || 'Anonymous';
    },
  },
});
